<template>
    <div id="header" style="width:100%;height:65px;background:#242f42;position:fixed;z-index:1;">
        <tip v-show="false" @func="getTipNumToDot"></tip>
        <el-row>
            <el-col :span="24">
                <div class="grid-content bg-purple" style="width:35%;">
                    <div class="logo">
                        <img :src="logo" alt="玉带职业技术学校">
                    </div>
                    <div class="title">
                        <p style="letter-spacing:10px;">玉带职业技术学校<p/>
                        <div class="l_title">教学诊断与改进信息系统</div>
                    </div>
                </div>
                <div class="grid-content bg-purple-light" style="width:65%;float:right;">
                    <div style="float:right;">
                        <el-input
                            placeholder="请输入内容"
                            prefix-icon="el-icon-search"
                            v-model="search"
                            clearable
                            style="width:230px;margin-top:12px;">
                        </el-input>
                        <el-button 
                        type="primary" 
                        icon="el-icon-search" 
                        style="margin-top:12px;background-color: #029348;border:1px solid #029348;">
                            搜索
                        </el-button>
                    <ul class="headerNav">
                        <li class="userInfo">
                            <div class="info">
                                <span class="name">{{name}}</span>
                                <span class="zw">教师</span>
                            </div>
                            <div class="tx">
                                <router-link to="/stip">
                                <img :src="touxiang" alt="">
                                <span class="tx_dot">
                                    <el-badge 
                                    is-dot 
                                    class="item"
                                    v-if="isTip"></el-badge>
                                </span>
                                    <!-- <img :src="touxiang" alt=""> -->
                                </router-link>
                            </div>
                        </li>
                        <li class="loginout">
                            <el-dropdown trigger="click" @command="handleCommand">
                                <span class="el-dropdown-link">
                                    <div class="tuichu">
                                        <i class="iconfont icon-tuichu" style="font-size:20px;"></i>
                                    </div>
                                </span>
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item command="1">诊改数据深度挖掘平台</el-dropdown-item>
                                    <el-dropdown-item command="2">诊改双链工作管理平台</el-dropdown-item>
                                    <el-dropdown-item command="3">诊改课堂数据统计平台</el-dropdown-item>
                                    <el-dropdown-item command="4">诊改校本数据中心</el-dropdown-item>
                                    <el-dropdown-item command="5">诊改大屏全景展示平台</el-dropdown-item>
                                    <el-dropdown-item command="6">退出登录</el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown>
                        </li>
                    </ul>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import tip from "./stationTip.vue"
export default {
    components:{
        tip
    },
    data(){
        return{
			isTip: false,
            logo: 'http://cdn.xqit.net/zg/img/yd2.png',
            touxiang: 'http://cdn.xqit.net/zg/img/laoshi.png',
            search:'',
            name: '',
        }
    },
    methods:{
		getTipNumToDot(data){
            if (data != 0) {
                this.isTip = true;
            }else{
                this.isTip = false;
            }
            // this.isTip = data;
            // console.log(data, '这里是主页的data')
            // console.log(this.isTip, '这里是主页的istip')
        },
        handleCommand(command){
            console.log(command)
            var _this = this
            var user=window.sessionStorage.getItem('user')
            var user_id=window.sessionStorage.getItem('user_id')
            var userdj=window.sessionStorage.getItem('userdj')
            var usergender=window.sessionStorage.getItem('usergender')
            if(command == '2'){
                //   window.location.href = 'http://zg.demo.xqit.net:81/#/zgmb'+'?name='+user+'&dj='+userdj+'&gender='+usergender+'&user_id='+user_id
                 window.location.href = '/ydjxzg/shuanglianguanli/index.html#/zgmb'+'?name='+user+'&dj='+userdj+'&gender='+usergender+'&user_id='+user_id
                 
            }
            if(command == '6'){
                window.sessionStorage.removeItem('login')
                window.sessionStorage.removeItem('user')
                window.sessionStorage.removeItem('user_id')
                window.sessionStorage.removeItem('userdj')
                window.sessionStorage.removeItem('usergender')
                $.ajax({
                    type: "post",
                    url: _this.baseUrl+"/ydjxzg/log/logout.action",
                    data: {},
                    dataType: "json",
                    success: function (res) {
                        console.log(res)
                        _this.$message.success('您已退出登录')
                        _this.$router.push({path:'/login'})
                    },
                });
                
            }
        }
    },
    mounted(){
        // console.log(window.sessionStorage.getItem('user'))
        this.name = window.sessionStorage.getItem('user')
    }
}
</script>

<style scoped>
.tx_dot{
    position: absolute;
    right: 0px;
    /* left: 10px; */
    top: -3px;
}

.grid-content{
    height: 65px;
    float: left;
    overflow: hidden;
}
.logo{
    width: 65px;
    height: 65px;
    float: left;
    /* margin: 0 20px 0; */
}
.logo img{
    width: 65px;
    height: 65px;
}
.title{
    /* width: 370px; */
    height: 27px;
    float: left;
    font-size: 20px;
    line-height: 27px;
    padding-left: 30px;
    text-align: left;
    color: #fff;
    display: inline-block;
    margin-top: 10px;
}
.l_title{
    float: left;
    font-size: 14px;
    text-align: left;
    color: #fff;
    letter-spacing: 7.5px;
    font-family: 'KaiTi';
}
.headerNav{
    float: right;
    display: flex;
    color: #fff;
}
.userInfo{
    width: 80px;
    height: 65px;
    padding: 0 20px;
    cursor: pointer;
    display: flex;
    align-items: center;
}
.info{
    width: 42px;
    height: 36px;
    float: left;
    line-height: 18px;
    margin-right: 5px;
}
.name{
    display: block;
    text-align: right;
    font-size: 14px;
}
.zw{
    color: rgb(186, 186, 186);
    font-size: 12px;
    float: right;
    text-align: right;
}
.tx{
    width: 30px;
    height: 30px;
    float: right;
    position: relative;
}
.tx img{
    width: 100%;
    height: 100%;
}
.loginout{
    line-height: 65px;
    padding: 0 20px;
    cursor: pointer;
}
.tuichu{
    color: #ffffff;
}
</style>



