<template>
    <div id="radarCharts" :style="radarStyle.style">
        <div id="myRadarChart" style="width:100%;height:100%;"></div>
    </div>
</template>

<script>
export default {
    props:['radarData','radarStyle'],
    data(){
        return{

        }
    },
    methods:{
        back(){
            this.drawRadar();
        },
        drawRadar(){
            let myRadar = this.$echarts.init(document.getElementById('myRadarChart'));
            console.log(myRadar)
            myRadar.setOption(this.radarData)
        }
    },
    mounted(){
        this.drawRadar()
        let myRadar = this.$echarts.init(document.getElementById('myRadarChart'));
        myRadar.on('click', function (params) {
            console.log(params);
            // myRadar.setOption(_this.option)
        });
    }
}
</script>

