<template>
    <div class="hello">
        <div class="header">
            <v-header></v-header>
        </div>
        <div id="main">
            <div class="aside">
                <v-nav></v-nav>
            </div>
            <div class="content">
                <div class="view">
                    <router-view></router-view>
                </div>
                <v-footer></v-footer>
            </div>
        </div>


        <!-- <div class="box" style="width:100%;">
            <v-bar :op="baroption" :st="barstyle" style="margin-right:20px;"></v-bar>
        </div> -->
        <!-- <v-tree :treeData="treedata" :treeStyle="treestyle"></v-tree> -->
        <!-- <v-line :lineData="lineOption" :lineStyle="linestyle"></v-line> -->
        <!-- <v-radar :radarData="radarOption" :radarStyle="radarstyle"></v-radar> -->
        <!-- <v-bl :blData="bl" :blStyle="blstyle"></v-bl> -->
        <!-- <Doughnut/> -->
        <!-- <v-map :mapData='mapOption' :mapStyle='mapstyle'></v-map> -->
    </div>
</template>

<script>
import Header from './header.vue';
import footer from './footer.vue';
import barCharts from './barCharts.vue';
import treeCharts from './treeCharts.vue';
import radarCharts from './radarCharts.vue';
import lineCharts from './lineCharts.vue';
import bl from './contrast';
import mapCharts from './map.vue';
import Doughnut from './Doughnut.vue';
import nav from './nav.vue';
export default {
    name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            barstyle:{
                style:'width:40%;height:400px;'
            },
            treestyle:{
                style:'width:100%;height:500px;'
            },
            radarstyle:{
                style:'width:80%;height:400px;'
            },
            linestyle:{
                style: 'width:40%;height:400px;'
            },
            blstyle:{
                style: 'width:80%;height:160px;'
            },
            mapstyle:{
                style:'width:80%;height:500px;'
            },
            baroption:{
                title: { text: '历年教职工人数' },
                toolbox: {
                    show: true,
                    feature: {
                        dataView: {readOnly: false},
                        magicType: {type: ['line', 'bar']},
                        restore: {},
                        saveAsImage: {}
                    }
                },
                color: ['#3398DB'],
                tooltip: {
                    trigger: 'axis',
                    axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                      type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                    }
                  },
                dataZoom: [
                    {
                        type: 'slider',
                        show: true,
                        start: 0,
                        end: 100,
                        handleSize: 8
                    },
                    {
                        type: 'inside',
                        start: 94,
                        end: 100
                    },
                      // {
                      //     type: 'slider',
                      //     show: true,
                      //     yAxisIndex: 0,
                      //     filterMode: 'empty',
                      //     width: 12,
                      //     height: '70%',
                      //     handleSize: 8,
                      //     showDataShadow: false,
                      //     left: '93%'
                      // }
                ],
                xAxis: [
                    {
                        type : 'category',
                        data: ['信息工程系', '机电工程汽车系', '人文系', '财贸经济系', '外语系', '规划建筑工程系', '园林与食品工程学院', '互联网学院', '艺术系'],
                        axisTick: {
                            // alignWithLabel: true,
                        },
                        axisLabel: {
                            interval:0,
                            rotate:-20
                        }
                    }
                ],
                yAxis: [
                    {
                        name:'(人)',
                        type : 'value'
                    }
                ],
                series: [{
                    name: '人数',
                    type: 'bar',
                    data: [570, 500, 546, 615, 910, 1016,1000,1115,1020],
                    itemStyle: {
                        normal: {
                        label: {
                            show: true, //开启显示
                            position: 'top', //在上方显示
                            textStyle: { //数值样式
                            color: '#3398DB',
                            fontSize: 14
                            }
                        }
                        }
                    }
                }]
            },
            treedata:{
            'name':'aaa',
                'children':[
                    {
                    'name':'a1',
                    'children':[
                        {'name':'a11','value':'111'},
                        {'name':'a12','value':'112'},
                        {'name':'a13','value':'113'},
                        {'name':'a14','value':'114'}
                    ]
                    },
                    {
                    'name':'a2',
                    'children':[
                        {'name':'a11','value':'111'},
                        {'name':'a12','value':'112'},
                    ]
                    },
                    {
                    'name':'a3',
                    },
                    {
                    'name':'a4',
                    'children':[
                        {'name':'a11','value':'111'}
                    ]
                    },
                    {
                    'name':'a5',
                    'children':[
                        {
                        'name':'a11',
                        'children':[
                            {'name':'b1','value':'222'},
                            {'name':'b2','value':'2332'},
                            {'name':'b3','value':'2442'},
                        ]
                        },
                        {'name':'a12','value':'112'},
                        {'name':'a13','value':'113'},
                        {'name':'a14','value':'114'}
                    ]
                    },
                    {
                    'name':'a6',
                    'children':[
                        {'name':'a11','value':'111'},
                        {'name':'a12','value':'112'},
                        {'name':'a13','value':'113'},
                        {'name':'a14','value':'114'}
                    ]
                    },
                ]
            },
            radarOption:{
                title: {
                    text: '自定义雷达图'
                },
                toolbox: {
                    show: true,
                    feature: {
                        saveAsImage: {}
                    }
                },
                tooltip: {
                    trigger: 'axis'
                },
                radar: [{        // 雷达图坐标系组件，只适用于雷达图。
                    center: ['50%', '50%'],         // 圆中心坐标，数组的第一项是横坐标，第二项是纵坐标。[ default: ['50%', '50%'] ]
                    radius: 100,             // 圆的半径，数组的第一项是内半径，第二项是外半径。
                    startAngle: 90,          // 坐标系起始角度，也就是第一个指示器轴的角度。[ default: 90 ]
                    name: {             // (圆外的标签)雷达图每个指示器名称的配置项。
                        formatter: '{value}',
                        textStyle: {
                            fontSize: 14,
                            color: '#000'
                        }
                    },
                    nameGap: 15,              // 指示器名称和指示器轴的距离。[ default: 15 ]
                    splitNumber: 5,           // (这里是圆的环数)指示器轴的分割段数。[ default: 5 ]
                    shape: 'circle',          // 雷达图绘制类型，'polygon'(多边形)  'circle'(圆)。[ default: 'polygon' ]
                    axisLine: {               // (圆内的几条直线)坐标轴轴线相关设置
                        lineStyle: {
                            color: '#999',    // 坐标轴线线的颜色。
                            width: 1,         // 坐标轴线线宽。
                            type: 'solid',    // 坐标轴线线的类型。
                        }
                    },
                    splitLine: {             // (这里是指所有圆环)坐标轴在 grid 区域中的分隔线。
                        lineStyle: {
                            color: '#999',      // 分隔线颜色
                            width: 1, 		    // 分隔线线宽
                        }
                    },
                    splitArea: {                // 坐标轴在 grid 区域中的分隔区域，默认不显示。
                        show: true,
                        areaStyle: {            // 分隔区域的样式设置。
                            color: ['rgba(250,250,250,0.3)','rgba(200,200,200,0.3)'],       // 分隔区域颜色。分隔区域会按数组中颜色的顺序依次循环设置颜色。默认是一个深浅的间隔色。
                        }
                    },
                    indicator: [{               // 雷达图的指示器，用来指定雷达图中的多个变量（维度）,跟data中 value 对应
                        name: '道德修养（立德）',           // 指示器名称   
                        max: 100,             // 指示器的最大值，可选，建议设置 
                        color: '#999'         // 标签特定的颜色。
                    }, {
                        name: '学业发展（立学）',
                        max: 100,
                        color: '#999'
                    }, {
                        name: '素质能力（立能）',
                        max: 100,
                        color: '#999'
                    }, {
                        name: '生活能力（立身）',
                        max: 100,
                        color: '#999'
                    }]
                }],
                series:[{
                    name:'雷达图',
                    type:'radar',
                    symbol:'rect',
                    symbolSize: 5,
                    tooltip: {
                        trigger: 'item'
                    },
                    itemStyle:{
                        normal:{
                            lineStyle:{
                                width: 1,
                                color: 'red',
                            },
                            opacity:1
                        }
                    },
                    data:[{
                        name:'学校学生建设质量指数',
                        value:[51,49,50,50],
                        areaStyle: {
                            normal: {
                                opacity: 0.8,
                                color: 'red'
                            }
                        },
                        symbol: 'rect',
                        symbolSize: 5
                    }]
                }]
            },
            lineOption:{
                title: {
                    text: '折线图'
                },
                tooltip: {
                    trigger: 'axis'
                },
                xAxis: {
                    type: 'category',
                    data: ['信息工程系', '机电工程汽车系', '人文系', '财贸经济系', '外语系', '规划建筑工程系', '园林与食品工程学院', '互联网学院', '艺术系'],
                    axisLabel: {
                        interval:0,
                        rotate:-20
                    }
                },
                yAxis: {
                    type: 'value'
                },
                series: [{
                    data: [820, 932, 901, 934, 1290, 1330, 1320,1545,565],
                    type: 'line',
                    color: '#3398DB',
                    smooth: true,
                    itemStyle: {
                    normal: {
                        label: {
                        show: true, //开启显示
                        position: 'top', //在上方显示
                        textStyle: { //数值样式
                            color: '#3398DB',
                            fontSize: 14
                        }
                        }
                    }
                    },
                }]
            },
            bl:{
                count: 100,
                left: 30,
                right: 70,
                ssbl: '14:1'
            },
            mapOption:{
                selectProe: '河南',
                proedata: [
                    {name:'郑州市',value:'40'},
                    {name:'商丘市',value:'4'},
                    {name:'开封市',value:'24'},
                    {name:'洛阳市',value:'14'},
                ],
                citydata:[
                    {name:'二七区',value:'22'},
                    {name:'中原区',value:'44'},
                ]
            }
        }
    },
  components:{
    'v-tree': treeCharts,
    'v-bar': barCharts,
    'v-radar': radarCharts,
    'v-header': Header,
    'v-footer': footer,
    'v-line': lineCharts,
    'v-bl': bl,
    'v-map': mapCharts,
    Doughnut,
    'v-nav': nav
  },
   methods:{

  },
  mounted(){
     var Main = document.getElementById("main");
     var wh = document.body.clientHeight;;
     Main.style.height = (wh - 66) + 'px'
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .hello{
        width: 100%;
        height: 100%;
    }
    .header{
        width: 100%;
        height: 65px;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 999;;
    }
    #main{
        width: 100%;
        /* height: 100%; */
        background: #ffffff;
        color: #333;
        padding-top: 65px;
        display: flex;
    }
    .aside{
        /* width: 200px; */
        float: left;
    }
    .content{
        overflow: scroll;
        overflow-x: hidden;
        flex: 1;
    }
    .view{
        min-height: 85%;
        padding: 0 2% 50px;
    }
</style>



