<template>
    <div id="treeCharts"  :style="treeStyle.style">
        <div class="back" @click="back()" ref="back">返回</div>
        <div id="myTreeChart" style="width:100%;height:100%;"></div>
    </div>
</template>

<script>
export default {
    props:["treeData","treeStyle"],
    data() {
        return {
            
        }
    },
    methods: {
        back(){
            this.drawTree();
        },
        drawTree(){
            var option = this.treeData;
            let myTree = this.$echarts.init(document.getElementById('myTreeChart'));
            myTree.setOption({
                tooltip: {
                    trigger: 'item',
                    triggerOn: 'mousemove'
                },
                toolbox: {
                    show: true,
                    feature: {
                        saveAsImage: {}
                    }
                },
                series: [
                    {
                        type: 'tree',
                        data: [option],
                        top: '1%',
                        left: '7%',
                        bottom: '1%',
                        right: '20%',
                        symbolSize: 10,
                        roam: true,
                        label: {
                            normal: {
                                position: 'left',
                                verticalAlign: 'middle',
                                align: 'right',
                                fontSize: 12
                            }
                        },
                        leaves: {
                            label: {
                                normal: {
                                    position: 'bottom',
                                    verticalAlign: 'middle',
                                    align: 'left'
                                }
                            }
                        },
                        expandAndCollapse: true,
                        animationDuration: 550,
                        animationDurationUpdate: 750
                    }
                ]
            });

        }
        
    },
    mounted(){
        this.drawTree();
        let myTreeChart = this.$echarts.init(document.getElementById('myChart'));
        myTreeChart.on('click', function (params) {
            console.log(params);
        });
    }
}
</script>

<style scoped>
    .back{
        width: 100px;
        height: 20px;
        font-size: 18px;
        color: #3398DB;
        cursor: pointer;
    }
</style>



