<template lang="html">
  <div class="doughnut">
    <div id="myChart1" style="width: 640px; height: 500px;"></div>
    <button @click="back">返回</button>
  </div>
</template>

<script>
export default {
  name: "doughnut",
  data () {
    return {
      option: {
        title : {
              text: '某站点用户访问来源',
              subtext: '',
              x:'left'
          },
          tooltip: {
              trigger: 'item',
              formatter: "{a} <br/>{b}: {c} ({d}%)"
          },
          series: [
              {
                  name:'访问来源',
                  type:'pie',
                  radius: ['40%', '60%'],
                  avoidLabelOverlap: false,
                  label: {
                      normal: {
                          show: true,
                          position: 'outer'
                      },
                      emphasis: {
                          show: true,
                          textStyle: {
                              fontSize: '16',
                              fontWeight: 'bold'
                          }
                      }
                  },
                  labelLine: {
                      normal: {
                          show: true
                      }
                  },
                  data:[
                      {value:335, name:'直接访问'},
                      {value:310, name:'邮件营销'},
                      {value:234, name:'联盟广告'},
                      {value:135, name:'视频广告'},
                      {value:1548, name:'搜索引擎'}
                  ]
              }
          ]
      },
      option2: {
        title : {
              text: '某站点用户访问来源',
              subtext: '',
              x:'left'
          },
          series: [
              {
                  name:'访问来源',
                  type:'pie',
                  radius: ['40%', '60%'],
                  avoidLabelOverlap: false,
                  label: {
                      normal: {
                          show: true,
                          position: 'outer'
                      },
                      emphasis: {
                          show: true,
                          textStyle: {
                              fontSize: '16',
                              fontWeight: 'bold'
                          }
                      }
                  },
                  labelLine: {
                      normal: {
                          show: true
                      }
                  },
                  data:[
                      {value:335, name:'百度'},
                      {value:310, name:'搜狗'},
                      {value:234, name:'360'}
                  ]
              }
          ]
      },

    }
  },
  mounted () {
    this.drawLine()
    // 数据下潜
    var _this = this
    let myChart1 = this.$echarts.init(document.getElementById('myChart1'))
    myChart1.on('click', function (params) {
        // console.log(params)
        _this.option2.title.text = params.name
        myChart1.setOption(_this.option2)
    })
  },
  methods: {
    drawLine: function () {
      // 基于准备好的dom，初始化echarts实例
        let myChart1 = this.$echarts.init(document.getElementById('myChart1'))
        // 绘制图表
        var option = this.option
        myChart1.setOption(option)
    },
    back: function () {
      this.drawLine()
    }

  }
}
</script>

<style lang="css">
</style>



