<template>
<div>
    <div class="cardtitle">{{title}}</div>
    <div id="map" style="width:100%;height:400px;">
        <div class="back" @click="back()" ref="back">返回</div>
        <div :id="mapData.proe_map" style="width:550px;height:400px;margin:0 auto;"></div>
        <div :id="mapData.city_map" style="width:550px;height:400px;margin:0 auto;display:none;"></div>
    </div>
</div>
   
</template>

<script>
export default {
    name: 'mapchart',
    props:['mapData'],
    data(){
        return{
            title:' ',
            opation:{
                
            }
        }
    },
    methods:{
        back(){
            this.drawMap()
        },
        drawMap(){
            // myMap.setOption(optionMap);
            //    调取后台数据
            var _this = this;
            var selectProe = this.mapData.selectProe;
            var proedata = this.mapData.proedata;
            $.get('static/json/'+selectProe+'/'+selectProe+'.json', function (Citymap) {
                echarts.registerMap(selectProe, Citymap);
                var myChartProe = echarts.init(document.getElementById(_this.mapData.proe_map));
                myChartProe.setOption({
                    tooltip: {
                        trigger: 'item',
                        formatter: function (result){//回调函数，参数params具体格式参加官方API
                            if(result.value){
                                return result.name+':'+result.value+'人';
                            }else{
                                return '';
                            }
                        } 
                    },
                    dataRange:{
                        min:0,  
                        max:50,  
                        splitNumber:0,
                        text:['高','低'],  
                        realtime:false,
                        calculable:true,
                        selectedMode:false,
                        realtime:false,
                        show:false,
                        // itemWidth:10,
                        // itemHeight:60,
                        inRange: {
                            color: ['lightskyblue','yellow', 'orangered']
                        }
                    },
                    series: [{
                        type: 'map',
                        map: selectProe ,//要和echarts.registerMap（）中第一个参数一致
                        scaleLimit: { min: 0.8, max: 1.9 },//缩放
                            mapLocation:{  
                            y:60  
                        },  
                        itemSytle:{  
                            emphasis:{label:{show:false}}  
                        }, 
                        label: {
                            normal: {
                                show: true
                            },
                            emphasis: {
                                show: true
                            }
                        },
                        data:proedata
                    }]
                })
                myChartProe.on('click',function(rel){
                        setTimeout(function () { 
                            function contains(arr, obj) {  
                            var i = arr.length;  
                            while (i--) {  
                                if (arr[i] === obj) {  
                                    return true;  
                                }  
                            }  
                            return false;  
                        }
                        var arr = new Array('北京','上海','天津','台湾','重庆');
                            if(contains(arr,selectProe)==false){
                                $('#proe_map').css('display','none');
                                $('#city_map').css('display','block');
                            }
                            else{
                                $('#proe_map').css('display','block');
                                $('#city_map').css('display','none');
                            }
                        }, 0);
                        $('#proe_map').css('display','none');
                        $('#city_map').css('display','block');
                    //选择市的单击事件
                    var selectCity = rel.name;
                    //    调取后台数据
                    $.get('static/json/'+selectProe+'/'+selectCity+'.json', function (Citymap) {
                        echarts.registerMap(selectCity, Citymap);
                        var myChartCity = echarts.init(document.getElementById(_this.mapData.proe_map));
                        myChartCity.setOption({
                            title:{
                                // text:'当前学年招生地域分布',
                                // //subtext:'',
                                // // x:'center',
                                // // y:'top',
                                // textAlign:'left'
                            },
                            tooltip: {
                                trigger: 'item',
                                formatter: function (result){//回调函数，参数params具体格式参加官方API
                                    if(result.value){
                                        return result.name+':'+result.value+'人';
                                    }else{
                                        return '';
                                    }
                                } 
                            },
                            dataRange:{
                                min:0,  
                                max:50,  
                                splitNumber:0,
                                text:['高','低'],  
                                realtime:false,
                                calculable:true,
                                selectedMode:false,
                                realtime:false,
                                show:false,
                                // itemWidth:10,
                                // itemHeight:60,
                                inRange: {
                                    color: ['lightskyblue','yellow', 'orangered']
                                }
                            },
                            series: [{
                                type: 'map',
                                map: selectCity ,//要和echarts.registerMap（）中第一个参数一致
                                scaleLimit: { min: 0.8, max: 1.9 },//缩放
                                    mapLocation:{  
                                    y:60  
                                },  
                                itemSytle:{  
                                    emphasis:{label:{show:false}}  
                                }, 
                                label: {
                                    normal: {
                                        show: true
                                    },
                                    emphasis: {
                                        show: true
                                    }
                                },
                                data : [
                                    {name:'二七区',value:'22'},
                                    {name:'中原区',value:'44'},
                                    {name:'大方县',value:'5'},  
                                    {name:'金沙县',value:'0'},  
                                    {name:'织金县',value:'10'},  
                                    {name:'七星关区',value:'30'},  
                                    {name:'纳雍县',value:'50'},  
                                    {name:'赫章县',value:'15'}
                                ]//dataParam//人口数据：例如[{name:'济南',value:'100万'},{name:'菏泽'，value:'100万'}......]
                            }]
                        })
                        myChartCity.on('click',function(rel){
                            setTimeout(function () { 
                                //$('#cont_pro_map').css('display','block');
                                //$('#cont_city_map').css('display','none');
                            }, 500);
                            
                        })
                    });
                        
                })
            });
        }
    },
    mounted(){
        this.title = this.mapData.title;
        this.drawMap()
    }
}
</script>

<style scoped>
.back{
    font-size: 16px;
    font-weight: 900;
    color: #029348;
    cursor: pointer;
    text-align: left;
    float: right;
    position: relative;
    right: 130px;
    top: 30px;
    z-index: 9999;
}
</style>




