<template>
    <div id="jzgrs" :style="blStyle.style">
        <div class="jzgrs_box">
            <div class="rs">
                <div class="rs_left">
                    <span class="icon">0</span>
                </div>
                <div class="rs_right">
                    <div class="rsTips">在职教职工人数</div>
                    <div class="num">
                        <span style="font-size:24px;">{{blData.count}}</span> 人
                    </div>
                </div>
            </div>
            <div class="bl">
                <div class="gender">
                    <div class="bl_left bl_num">
                        <span>0</span>
                        <div class="man_num">{{blData.left}}人</div>
                    </div>
                    <div class="bl_right bl_num">
                        <span style="background: #fff2f5;border: 1px solid #ffdae3;">0</span>
                        <div class="woman_num">{{blData.right}}人</div>
                    </div>
                </div>
                <div class="gender_line">
                    <div class="man_bl_num">男{{gender_rate_left}}</div>
                    <div class="gender_rate">
                        <span class="gender_rate_man" :style="leftWidth"></span>
                        <span class="gender_rate_woman" :style="rightWidth"></span>
                    </div>
                    <div class="woman_bl_num">女{{gender_rate_right}}</div>
                </div>
            </div>
            <div class="shisheng">
                <div class="ss_left">
                    <span class="icon">0</span>
                </div>
                <div class="ss_right">
                    <div class="title">师生比</div>
                    <span class="num">{{blData.ssbl}}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props:['blData','blStyle'],
    data(){
        return{
            gender_rate_left: '50%',
            gender_rate_right: '50%',
            leftWidth: null,
            rightWidth: null,
        }
    },
    method:{

    },
    mounted(){
        this.gender_rate_left = Math.round((this.blData.left/this.blData.count)*100) + '%';
        this.gender_rate_right = Math.round((this.blData.right/this.blData.count)*100) + '%';
        this.leftWidth = 'width:' + this.gender_rate_left;
        this.rightWidth = 'width:' + this.gender_rate_right;
    }
}
</script>

<style scoped>
    #jzgrs{
        width: 80%;
        margin-top: 20px;
        border: 1px solid #ebeef5;
        box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    }
    .jzgrs_box{
        width: 100%;
        height: 160px;
        border: 1px solid #ebeef5;
        display: flex;
    }
    .rs{
        width: 25%;
        height: 161px;
        margin-top: -1px;
        background: #5b8fe4;
        border-radius: 4px;
        color: #fff;
    }
    .rs_left{
        width: 70px;
        height: 160px;
        margin-left: 10px;
        float: left;
    }
    .rs_left .icon{
        display: block;
        font-size: 24px;
        line-height: 160px;
        text-align: center;
    }
    .rs_right{
        float: left;
        font-size: 14px;
        margin-top: 45px;
    }
    .rs_right .num{
        margin-top: 5px;
    }
    .bl{
        flex: 1;
    }
    .gender{
        width: 100%;
        height: 52px;
        margin: 32px 0;
        display: flex;
        justify-content: center;
    }
    .bl_num{
        flex: 1;
        height: 52px;
        font-size: 23px;
        color: #526069;
        line-height: 52px;
        text-align: center;
        display: flex;
        justify-content: center;
    }
    .bl_num span{
        display: inline-block;
        width: 52px;
        height: 52px;  
        font-size: 26px;
        border-radius: 50%;
        border: 1px solid #d9e8fe;
        background: #ebf3ff;
        line-height: 52px; 
        float: left;
        margin-right: 3px;
    }
    .gender_line{
        width: 100%;
        height: 16px;
        font-size: 12px;
        display: flex;
        justify-content: center;
    }
    .man_bl_num{
        color: #62a8ea;
    }
    .woman_bl_num{
        color: #ff8b91;
    }
    .gender_rate{
        width: 65%;
        height: 12px;
        margin-top: 2px;
    }
    .gender_rate_man{
        display: inline-block;
        width: 48%;
        height: 8px;
        margin-top: 2px;
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
        background: #62a8ea;
        float: left;
    }
    .gender_rate_woman{
        display: inline-block;
        width: 52%;
        height: 8px;
        margin-top: 2px;    
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
        background: #ff8b91;
        float: left;
    }
    .shisheng{
        width: 25%;
        height: 160px;
    }
    .ss_left{
        width: 30%;
        height: 160px;
        line-height: 160px;
        float: left;
        color: rgb(255, 192, 0);
        font-size: 47px;
        text-align: center;
    }
    .ss_right{
        float: left;
        padding-left: 8px;
    }
    .ss_right .title{
        height: 19px;
        margin-top: 45px;
        font-size: 14px;
        line-height: 19px;
        color: #a3afb7;
    }
    .ss_right .num{
        display: inline-block;
        margin-top: 5px;
        font-size: 24px;
        color: #526069;
    }
</style>



