            console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        getAllNav(){
            var that = this;
            $.ajax({
                type: "post",
                url: that.baseUrl+"/ydjxzg/log/getpermis.action",
                //url: "/ydjxzg/log/login.action",
                data: {
                    user_id: sessionStorage.getItem("user_id")
                },
                dataType: "json",
                success: function (res) {
                    that.all = res.permis
                    console.log(that.all);
                    that.$forceUpdate();
                }
            });
        }
    },
    created(){
        this.getAllNav();
    },
    mounted() {
        
